.landing {
  min-height: 70vh;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  // overflow-x: hidden;
  // overflow-y: unset;

  &__text {
    max-width: 37ch;
  }
  &__header {
    font-size: 2.5rem;
  }
  &__image {
    position: absolute;
    z-index: 0;

    &--pattern-desktop {
      left: 50%;
      top: 50%;
      transform: translateY(-50%);
    }
    &--pattern-mobile {
      display: none;
    }
    &--mobiles {
      z-index: 11;
      right: 0;
      top: 50%;
      transform: translate(10%, -50%);
      max-width: 50vw;
    }
  }
}
